@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
@charset 'utf-8';

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video { margin:0; padding:0; border:0; outline:0; font-size:62.5%; vertical-align:top; background:transparent; }

body { line-height:1; }

article,aside,details,figcaption,figure, footer,header,hgroup,menu,nav,section { display:block; }

ul,ol { list-style:none; }

blockquote, q { quotes:none; }

blockquote:before, blockquote:after,
q:before, q:after { content:''; content:none; }

a {
    margin:0; padding:0; font:inherit; background:transparent; text-decoration: none; color:inherit }


button {border: none; background: none; outline:0;}
table { width:100%; border-collapse:collapse; border-spacing:0; }
h1,h2,h3,h4,h5,h6 {font-weight: normal;}
img {max-width:100%}

/* change colours to suit your needs */
ins { background-color:#ff9; color:#000; text-decoration:none; }

/* change colours to suit your needs */
mark { background-color:#ff9; color:#000; font-style:italic; font-weight:bold; }

del { text-decoration: line-through; }

abbr[title], dfn[title] { border-bottom:1px dotted; cursor:help; }

/* change border colour to suit your needs */
hr {
    display:block;
    height:1px;
    border:0;
    padding:0;
}


* {box-sizing: border-box}
html {font-family:'Nanum Gothic', sans-serif; }

body {background-color: #eef2f7;}

.container {max-width:1024px; margin: 0 auto;}
.loader{display:flex;justify-content:center;align-items:center;width:100%;height:100vh}.loader .loader_txt{font-size:2rem;font-weight:700}.movies_wrap{display:flex;flex-wrap:wrap;padding:60px 0;transition:0.5s all}.movie{position:relative;width:calc(50% - 30px);margin:3rem 15px;padding:30px 30px 30px 180px;box-shadow:0 13px 27px -5px rgba(50,50,93,0.25);background:#fff;transition:0.5s all}.movie img{width:135px;position:absolute;left:20px;top:-20px;box-shadow:0 13px 27px -5px rgba(50,50,93,0.1)}.movie .movie_title{padding-bottom:1.4rem;font-size:1.8rem;font-weight:700;letter-spacing:-1px;color:#35395d}.movie .movie_year{padding-bottom:1.6rem;font-size:1.2rem;color:#c3c7dd}.movie .movie_summary{padding-bottom:1.6rem;font-size:1.6rem;color:#4d4d4d;line-height:1.2;overflow:hidden;word-wrap:break-word}.movie .genres{display:flex;flex-wrap:wrap}.movie .genres_genre{padding-bottom:1rem;padding-right:1rem;font-size:1.4rem;color:#c3c7dd}@media (max-width: 1024px){.movies_wrap{justify-content:center;transition:0.5s all}.movie{width:calc(100% - 30px);max-width:600px;transition:0.5s all}}

